
.cardd {
    margin: auto; 
    padding: 20px;
    width: 90%; 
    max-width: 900px; 
    box-shadow: 0 4px 8px rgba(23, 21, 21, 0.1); 
    border-radius: 14px; 
    background-color: #fff; 
    margin-top: -120px;
}

.select-container {
    display: flex;
    justify-content: space-between; 
    gap: 16px; 
}

.select-item {
    flex: 1; 
}

.buttttt{
    padding:10px;
    width: 120px;
    cursor: pointer;
    border-radius: 20px;
    background-color: rgb(38, 38, 71);
    color: white;
    font-weight: 500;
}

.content {
    color: black;
    margin: 30px;
    padding: 10px;
}

.image-carousel {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 30px 0;
}

.carousel-image {
    width: 200px;
    height: 150px;
    object-fit: cover;
    border-radius: 20px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.arrow {
    cursor: pointer;
    font-size: 30px;
    margin: 0 20px;
    color: rgb(38, 38, 71);
}


.allimages {
    display: none;
}

@media (min-width: 768px) {
    .allimages {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        gap: 30px;
    }

    .allsize {
        width: 150px;
        height: 150px;
        object-fit: cover;
        border-radius: 20px;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
    }

    .image-carousel {
        display: none;
    }
}
